import { Card, CardContent } from '@/components/ui/card';
import { Progress } from '@/components/ui/progress';


export default function StatsGrid() {
    return (
        <div className="grid grid-cols-3 gap-4 mb-8">
            <Card className="bg-zinc-900 border-zinc-800">
                <CardContent className="p-4">
                    <p className="text-zinc-400 text-sm">Total Files Scanned</p>
                    <h2 className="text-3xl font-bold mt-2">384</h2>
                </CardContent>
            </Card>


            <Card className="bg-zinc-900 border-zinc-800">
                <CardContent className="p-4">
                    <p className="text-zinc-400 text-sm">Deprecated Syntax Fixed</p>
                    <h2 className="text-3xl font-bold mt-2">1,276</h2>
                </CardContent>
            </Card>


            <Card className="bg-zinc-900 border-zinc-800">
                <CardContent className="p-4">
                    <p className="text-zinc-400 text-sm">Overall Upgrade Progress</p>
                    <h2 className="text-3xl font-bold mt-2">71%</h2>
                    <Progress value={71} className="mt-3 bg-zinc-800" />
                </CardContent>
            </Card>
        </div>
    );
}